.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  user-select: "none";
}

.NumberButton {
  text-align: center;
  vertical-align: middle;
  line-height: 42px;
  width: 45px;
  height: 45px;
  font-size: x-large;
  font-weight: 900;
  color: white;
  background-color: rgb(33, 33, 33);
  border: 3px solid black;
  border-radius: 10px;
  user-select: none;
  margin: 2px;
}

.board {
    box-sizing: border-box;
  width: 450px;
  height: 450px;
  background-color: black;
}

.square {
  display: flex;
  box-sizing: border-box;
  flex-direction: row;
  flex-wrap: wrap;
  width: 150px;
  height: 150px;
  border: 3px solid black;
}

.row {
    position: relative;
  display: flex;
  width: 450px;
}
